body {
  margin: 0;
  padding: 0;
  overflow-wrap: break-word;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

div,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
p,
form,
fieldset,
input,
table,
tr,
th,
td,
button,
textarea,
select {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

[class] {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  border: 0;
  background-color: transparent;
  border-collapse: collapse;
  border-spacing: 0;
  -webkit-appearance: none;
  appearance: none;
}

[class]::before,
[class]::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

input {
  border-radius: 0;
}

button[class] {
  cursor: pointer;
}
button[class]:disabled {
  cursor: default;
}

[class*="--division"] {
  display: flex;
  justify-content: space-between;
}

[class*="--flexBtw"] {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

[class*="--flexArnd"] {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

[class*="--flexEven"] {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

[class*="--flexMid"] {
  display: flex;
  justify-content: center;
  align-items: center;
}

[class*="--flexCnt"] {
  display: flex;
  justify-content: normal;
  align-items: center;
}

[class*="--flex"] {
  display: flex;
}

[class*="--keepSize"] {
  flex-shrink: 0;
}

[class*="--columnBox"] {
  flex-direction: column;
  display: flex;
  justify-content: space-between;
}

[class*="--alignLeft"] {
  text-align: left;
}

[class*="--alignRight"] {
  text-align: right;
}

[class*="--alignCenter"] {
  text-align: center;
}

[class*="--floatLeft"] {
  float: left;
}

[class*="--floatRight"] {
  float: right;
}

[class*="--ellipsis"] {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

[class*="--strong"] {
  font-weight: 700;
}

[class*="--topSpace"] {
  margin-top: 64px;
}

[class*="--topFix"] {
  margin-top: -33px;
}

[class*="--topLine"] {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #eee;
}

.module-header {
  position: relative;
  z-index: 10;
}
.module-header__top {
  height: 56px;
  padding: 0 15px 0 16px;
  display: flex;
  justify-content: normal;
  align-items: center;
}
.module-header__logo {
  position: relative;
  width: 166px;
  height: 32px;
}
.module-header__logo > a {
  display: block;
  height: 100%;
}
.module-header__logo-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.module-header__logo-link {
  position: relative;
  margin-left: 6px;
}
.module-header__logo-link--active .ic_angle_down_circle_fill_s24 {
  transform: rotate(180deg);
}
.module-header__logo-link--active .module-linkPopup {
  display: block;
}
.module-header__logo-btn {
  vertical-align: top;
}
.module-header__logo-btn .ic_angle_down_circle_fill_s24 {
  transition: transform 0.25s linear, -webkit-transform 0.25s linear, -moz-transform 0.25s linear, -o-transform 0.25s linear;
}
.module-header__util {
  margin-left: auto;
  display: flex;
  justify-content: normal;
  align-items: center;
}
.module-header__btn {
  position: relative;
  width: 32px;
  height: 32px;
}
.module-header__under {
  position: relative;
  padding: 0 20px 19px;
}
.module-header__under .module-header__util {
  display: none;
}
.module-header--scroll .module-header__top {
  display: none;
}
.module-header--scroll .module-header__under {
  position: fixed;
  top: 0;
  width: 100%;
  max-width: 640px;
  height: 56px;
  padding: 0 15px;
  background-color: #333545;
  display: flex;
  justify-content: normal;
  align-items: center;
}
.module-header--scroll .module-header__under .module-header__util {
  display: block;
}
.module-header--scroll .module-header__under .module-header__desc {
  display: none;
}
.module-header--scroll .module-header__under .module-header__heading {
  font-size: 20px;
}

.module-header__heading {
  font-size: 22px;
  font-weight: 700;
  line-height: 27px;
}
.module-header__desc {
  margin-top: 7px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.module-header__desc--default {
  color: #d3d3d3;
}
.module-linkPopup {
  display: none;
  width: 200px;
  padding: 12px 0;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
}
.module-header__logo-link .module-linkPopup {
  position: absolute;
  top: 40px;
  right: 0;
  z-index: 2;
}

.module-linkPopup__item {
  height: 48px;
}
.module-linkPopup__item + .module-linkPopup__item {
  margin-top: 4px;
}
.module-linkPopup__item-btn {
  width: 100%;
  height: 100%;
  padding: 0 20px;
  text-align: left;
  display: flex;
  justify-content: normal;
  align-items: center;
}
.module-linkPopup__item-btn .linkThumb {
  position: relative;
  width: 32px;
  height: 32px;
  margin-right: 12px;
  background-color: #ddd;
  border-radius: 10px;
}
.module-linkPopup__item-btn .linkText {
  font-size: 16px;
  color: #333;
}
.module-linkPopup__item--active .linkThumb::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -3px;
  right: -3px;
  z-index: 1;
  width: 10px;
  height: 10px;
  border: 1px solid #fff;
  background-color: #ff5a5a;
  border-radius: 5px;
}
.module-linkPopup__item--active .linkText {
  font-weight: 700;
  color: #000;
}
.module-footer {
  background: #f5f5f5;
}
.module-footer__inner {
  padding: 40px 20px 94px;
}
.module-footer__links {
  display: flex;
  justify-content: center;
  align-items: center;
}
.module-footer__links > li {
  position: relative;
}
.module-footer__links > li + li {
  margin-left: 15px;
  padding-left: 15px;
}
.module-footer__links > li + li::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  width: 1px;
  height: 12px;
  background-color: #e2e2e2;
}
.module-footer__links > li a {
  font-size: 13px;
  font-weight: 700;
  color: #666;
}
.module-footer__copyright {
  margin-top: 22px;
  text-align: center;
}
.module-footer__copyright-txt {
  font-size: 12px;
  color: #888;
}

.o2o-wrap {
  position: relative;
  min-width: 280px;
  max-width: 640px;
  margin: 0 auto;
}
.o2o-wrap--flex-layout {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.o2o-wrap--flex-layout .container {
  flex: 1;
}
.o2o-wrap .module-header {
  background-color: #333545;
}
.o2o-wrap .module-footer__links {
  margin-bottom: 25px;
}

.swiper-banner-box {
  position: relative;
  padding: 20px;
}
.swiper-banner-box .swiper-banner {
  position: relative;
  border-radius: 12px;
}

.category-btn-box {
  padding: 0 20px;
  display: flex;
  justify-content: normal;
  align-items: center;
}
.category-btn-item + .category-btn-item {
  margin-left: 20px;
}

.category-btn {
  position: relative;
  padding-top: 54px;
  width: 51px;
  text-align: center;
}
.category-btn__text {
  font-size: 12px;
  color: #333;
}
.category-btn--call {
  background: url(https://apm-web.amoremall.com/public/resource/mall/svg/ic/ic_phone_s51_3a416f.svg) no-repeat;
}
.category-btn--reserve {
  background: url(https://apm-web.amoremall.com/public/resource/mall/svg/ic/ic_calendar_s51_3a416f.svg) no-repeat;
}
.category-btn--share {
  background: url(https://apm-web.amoremall.com/public/resource/mall/svg/ic/ic_share_s51_3a416f.svg) no-repeat;
}

.blind {
  position: absolute !important;
  z-index: -1;
  width: 1px !important;
  height: 1px !important;
  border: 0;
  clip: rect(1px 1px 1px 1px) !important;
  clip-path: inset(50%);
  overflow: hidden !important;
}

