body {
    margin: 0;
    padding: 0;
    overflow-wrap: break-word;
    -webkit-text-size-adjust: none;
    text-size-adjust: none;
}
div,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
p,
form,
fieldset,
input,
table,
tr,
th,
td,
button,
textarea,
select {
    margin: 0;
    padding: 0;
}
img {
    max-width: 100%;
    height: auto;
}
[class] {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    border: 0;
    background-color: transparent;
    border-collapse: collapse;
    border-spacing: 0;
    -webkit-appearance: none;
    appearance: none;
}
[class]::before,
[class]::after {
    box-sizing: border-box;
}
a {
    text-decoration: none;
}
input {
    border-radius: 0;
}
button[class] {
    cursor: pointer;
    &:disabled {
        cursor: default;
    }
}
